import { useState } from "react";

export const useLocalStorage = (key, initialValue) => {
  // Estado para armazenar o valor
  const [storedValue, setStoredValue] = useState(() => {
    try {
      const item = window.localStorage.getItem(key);
      return item ? JSON.parse(item) : initialValue;
    } catch (error) {
      console.error(`Erro ao ler localStorage key "${key}":`, error);
      return initialValue;
    }
  });

  // Função para definir valor
  const setValue = (value) => {
    try {
      // Permite que value seja uma função como useState
      const valueToStore =
        value instanceof Function ? value(storedValue) : value;
      setStoredValue(valueToStore);
      window.localStorage.setItem(key, JSON.stringify(valueToStore));
    } catch (error) {
      console.error(`Erro ao salvar localStorage key "${key}":`, error);
    }
  };

  return [storedValue, setValue];
};

// Hook específico para configurações
export const useSettings = () => {
  const [settings, setSettings] = useLocalStorage("calculatorSettings", {
    language: "auto",
    theme: "light",
    showImage: true,
    numberFormat: "exponential",
    timeFormat: "detailed",
    autoSave: true,
    notifications: true,
  });

  const updateSetting = (key, value) => {
    setSettings((prev) => ({
      ...prev,
      [key]: value,
    }));
  };

  return [settings, updateSetting, setSettings];
};

// Hook para perfis de cálculo
export const useProfiles = () => {
  const [profiles, setProfiles] = useLocalStorage("calculatorProfiles", {
    current: {
      name: "Atual",
      generationRate: "106.04",
      generationLevel: "w",
      currentGold: "0",
      currentGoldLevel: "w",
      targetAmount: "813.23",
      targetLevel: "y",
      useGoldVault: false,
      createdAt: new Date().toISOString(),
      lastUsed: new Date().toISOString(),
    },
    saved: [],
  });

  const saveProfile = (name, data) => {
    const newProfile = {
      id: Date.now().toString(),
      name,
      ...data,
      createdAt: new Date().toISOString(),
      lastUsed: new Date().toISOString(),
    };

    setProfiles((prev) => ({
      ...prev,
      saved: [newProfile, ...prev.saved.slice(0, 9)], // Máximo 10 perfis
    }));

    return newProfile;
  };

  const loadProfile = (profile) => {
    setProfiles((prev) => ({
      ...prev,
      current: {
        ...profile,
        lastUsed: new Date().toISOString(),
      },
    }));
  };

  const deleteProfile = (profileId) => {
    setProfiles((prev) => ({
      ...prev,
      saved: prev.saved.filter((p) => p.id !== profileId),
    }));
  };

  const updateCurrentProfile = (data) => {
    setProfiles((prev) => ({
      ...prev,
      current: {
        ...prev.current,
        ...data,
        lastUsed: new Date().toISOString(),
      },
    }));
  };

  return {
    profiles,
    saveProfile,
    loadProfile,
    deleteProfile,
    updateCurrentProfile,
    currentProfile: profiles.current,
    savedProfiles: profiles.saved,
  };
};

// Hook para histórico
export const useHistory = () => {
  const [history, setHistory] = useLocalStorage("calculatorHistory", []);

  const addToHistory = (calculation) => {
    const historyItem = {
      id: Date.now().toString(),
      timestamp: new Date().toISOString(),
      ...calculation,
    };

    setHistory((prev) => [historyItem, ...prev.slice(0, 49)]); // Máximo 50 itens
    return historyItem;
  };

  const removeFromHistory = (itemId) => {
    setHistory((prev) => prev.filter((item) => item.id !== itemId));
  };

  const clearHistory = () => {
    setHistory([]);
  };

  const toggleFavorite = (itemId) => {
    setHistory((prev) =>
      prev.map((item) =>
        item.id === itemId ? { ...item, isFavorite: !item.isFavorite } : item
      )
    );
  };

  const getFavorites = () => {
    return history.filter((item) => item.isFavorite);
  };

  return {
    history,
    addToHistory,
    removeFromHistory,
    clearHistory,
    toggleFavorite,
    getFavorites,
  };
};
