import { useEffect } from "react";

export const useThemeCustomization = (settings) => {
  useEffect(() => {
    console.log("🎨 useThemeCustomization executado!");

    const body = document.body;

    // Remover estilos anteriores
    const existingStyle = document.getElementById("theme-styles");
    if (existingStyle) {
      existingStyle.remove();
    }

    // Verificar se há cores personalizadas VÁLIDAS definidas pelo usuário
    const hasValidCustomColors =
      settings?.customColors &&
      settings.customColors.primary &&
      settings.customColors.primary !== "" &&
      settings.customColors.primary !== "#000000"; // Evita cor padrão preta

    console.log("🔍 Tem cores personalizadas válidas?", hasValidCustomColors);
    console.log("🔍 Tema atual:", settings?.theme);

    // Criar elemento de estilo apenas se necessário
    const style = document.createElement("style");
    style.id = "theme-styles";
    let themeCSS = "";

    if (hasValidCustomColors) {
      console.log("✅ APLICANDO CORES PERSONALIZADAS!");

      const { primary, secondary, accent, background } = settings.customColors;
      console.log("🎨 Cores aplicadas:", {
        primary,
        secondary,
        accent,
        background,
      });

      // Aplicar background apenas se definido
      if (background && background !== "" && background !== "#ffffff") {
        body.style.setProperty("background-color", background, "important");
      }

      themeCSS = `
        /* === CORES PERSONALIZADAS === */
        
        /* Background apenas se definido */
        ${
          background && background !== "" && background !== "#ffffff"
            ? `
        body { 
          background-color: ${background} !important; 
        }
        .bg-white { 
          background-color: ${background} !important; 
        }
        .bg-gray-50 { 
          background-color: ${background} !important; 
        }`
            : ""
        }
        
        ${
          secondary && secondary !== ""
            ? `
        .bg-gray-100 { 
          background-color: ${secondary} !important; 
        }`
            : ""
        }
        
        /* Textos com cor primária */
        .text-gray-800, .text-gray-900 { 
          color: ${primary} !important; 
        }
        .text-gray-600, .text-gray-700 { 
          color: ${primary} !important; 
          opacity: 0.8;
        }
        
        /* Títulos */
        h1, h2, h3, h4, h5, h6 { 
          color: ${primary} !important; 
        }
        
        /* Labels e textos importantes */
        label, .label-text { 
          color: ${primary} !important; 
        }
        
        /* Parágrafos em containers específicos */
        .bg-white p, .bg-gray-50 p { 
          color: ${primary} !important; 
        }
        
        ${
          accent && accent !== ""
            ? `
        /* Botões com cor de destaque */
        .bg-blue-600, .bg-indigo-600, 
        button.bg-blue-600, button.bg-indigo-600 { 
          background-color: ${accent} !important; 
          color: #ffffff !important; 
          border-color: ${accent} !important;
        }
        
        .hover\:bg-blue-700:hover, .hover\:bg-indigo-700:hover { 
          background-color: ${accent} !important; 
          filter: brightness(0.9) !important; 
        }
        
        /* Bordas */
        .border-gray-200, .border-gray-300 { 
          border-color: ${accent} !important; 
        }
        
        /* Inputs */
        input:focus, select:focus, textarea:focus {
          border-color: ${accent} !important;
          box-shadow: 0 0 0 3px ${accent}33 !important;
        }
        
        /* SVGs e ícones */
        svg.lucide, .lucide { 
          color: ${accent} !important; 
          stroke: ${accent} !important; 
        }`
            : ""
        }
      `;

      // Aplicar CSS das cores personalizadas
      style.textContent = themeCSS;
      document.head.appendChild(style);
    } else if (settings?.theme && settings.theme !== "light") {
      // Aplicar apenas temas predefinidos (não o light que é padrão)
      console.log("🎨 Aplicando tema predefinido:", settings.theme);

      switch (settings.theme) {
        case "dark":
          body.style.setProperty("background-color", "#111827", "important");
          themeCSS = `
            .bg-white { background-color: #1f2937 !important; }
            .bg-gray-50 { background-color: #111827 !important; }
            .bg-gray-100 { background-color: #374151 !important; }
            .text-gray-800, .text-gray-900 { color: #f9fafb !important; }
            .text-gray-600, .text-gray-700 { color: #d1d5db !important; }
            .border-gray-200 { border-color: #4b5563 !important; }
            h1, h2, h3, h4, h5, h6 { color: #f9fafb !important; }
            label, p { color: #f9fafb !important; }
          `;
          break;

        case "retro":
          body.style.setProperty("background-color", "#18181b", "important");
          themeCSS = `
            .bg-white { background-color: #27272a !important; border: 1px solid #3f3f46 !important; }
            .bg-gray-50 { background-color: #18181b !important; }
            .bg-gray-100 { background-color: #3f3f46 !important; }
            .text-gray-800, .text-gray-900 { color: #fde047 !important; font-weight: 500 !important; }
            .text-gray-600, .text-gray-700 { color: #facc15 !important; }
            .border-gray-200 { border-color: #eab308 !important; }
            .bg-blue-600, .bg-indigo-600 { background-color: #eab308 !important; color: #18181b !important; }
            .hover\:bg-blue-700:hover, .hover\:bg-indigo-700:hover { background-color: #ca8a04 !important; }
            h1, h2, h3, h4, h5, h6 { color: #fef9c3 !important; font-weight: 600 !important; }
            svg.lucide, .lucide { color: #fde047 !important; stroke: #fde047 !important; }
            label, p { color: #fef3c7 !important; font-weight: 500 !important; }
          `;
          break;

        case "ocean":
          body.style.setProperty("background-color", "#0f172a", "important");
          themeCSS = `
            .bg-white { background-color: #1e293b !important; border: 1px solid #334155 !important; }
            .bg-gray-50 { background-color: #0f172a !important; }
            .bg-gray-100 { background-color: #334155 !important; }
            .text-gray-800, .text-gray-900 { color: #e2e8f0 !important; font-weight: 500 !important; }
            .text-gray-600, .text-gray-700 { color: #94a3b8 !important; }
            .border-gray-200 { border-color: #475569 !important; }
            .bg-blue-600, .bg-indigo-600 { background-color: #0ea5e9 !important; }
            .hover\:bg-blue-700:hover, .hover\:bg-indigo-700:hover { background-color: #0284c7 !important; }
            h1, h2, h3, h4, h5, h6 { color: #f1f5f9 !important; font-weight: 600 !important; }
            svg.lucide, .lucide { color: #0ea5e9 !important; stroke: #0ea5e9 !important; }
            label, p { color: #cbd5e1 !important; font-weight: 500 !important; }
          `;
          break;

        case "forest":
          body.style.setProperty("background-color", "#0f1419", "important");
          themeCSS = `
            .bg-white { background-color: #1a2e1a !important; border: 1px solid #2d5a2d !important; }
            .bg-gray-50 { background-color: #0f1419 !important; }
            .bg-gray-100 { background-color: #2d5a2d !important; }
            .text-gray-800, .text-gray-900 { color: #e8f5e8 !important; font-weight: 500 !important; }
            .text-gray-600, .text-gray-700 { color: #a3d9a3 !important; }
            .border-gray-200 { border-color: #4ade80 !important; }
            .bg-blue-600, .bg-indigo-600 { background-color: #22c55e !important; }
            .hover\:bg-blue-700:hover, .hover\:bg-indigo-700:hover { background-color: #16a34a !important; }
            h1, h2, h3, h4, h5, h6 { color: #f0fdf4 !important; font-weight: 600 !important; }
            svg.lucide, .lucide { color: #4ade80 !important; stroke: #4ade80 !important; }
            label, p { color: #dcfce7 !important; font-weight: 500 !important; }
          `;
          break;

        case "sunset":
          body.style.setProperty("background-color", "#1c1917", "important");
          themeCSS = `
            .bg-white { background-color: #292524 !important; border: 1px solid #44403c !important; }
            .bg-gray-50 { background-color: #1c1917 !important; }
            .bg-gray-100 { background-color: #44403c !important; }
            .text-gray-800, .text-gray-900 { color: #fef3c7 !important; font-weight: 500 !important; }
            .text-gray-600, .text-gray-700 { color: #fbbf24 !important; }
            .border-gray-200 { border-color: #f59e0b !important; }
            .bg-blue-600, .bg-indigo-600 { background-color: #f59e0b !important; }
            .hover\:bg-blue-700:hover, .hover\:bg-indigo-700:hover { background-color: #d97706 !important; }
            h1, h2, h3, h4, h5, h6 { color: #fffbeb !important; font-weight: 600 !important; }
            svg.lucide, .lucide { color: #fbbf24 !important; stroke: #fbbf24 !important; }
            label, p { color: #fef3c7 !important; font-weight: 500 !important; }
          `;
          break;

        case "purple":
          body.style.setProperty("background-color", "#1e1b4b", "important");
          themeCSS = `
            .bg-white { background-color: #312e81 !important; border: 1px solid #4c1d95 !important; }
            .bg-gray-50 { background-color: #1e1b4b !important; }
            .bg-gray-100 { background-color: #4c1d95 !important; }
            .text-gray-800, .text-gray-900 { color: #e9d5ff !important; font-weight: 500 !important; }
            .text-gray-600, .text-gray-700 { color: #c4b5fd !important; }
            .border-gray-200 { border-color: #8b5cf6 !important; }
            .bg-blue-600, .bg-indigo-600 { background-color: #8b5cf6 !important; }
            .hover\:bg-blue-700:hover, .hover\:bg-indigo-700:hover { background-color: #7c3aed !important; }
            h1, h2, h3, h4, h5, h6 { color: #f5f3ff !important; font-weight: 600 !important; }
            svg.lucide, .lucide { color: #c4b5fd !important; stroke: #c4b5fd !important; }
            label, p { color: #e9d5ff !important; font-weight: 500 !important; }
          `;
          break;

        case "cyberpunk":
          body.style.setProperty("background-color", "#0a0a0a", "important");
          themeCSS = `
            .bg-white { background-color: #1a1a1a !important; border: 1px solid #00ff88 !important; }
            .bg-gray-50 { background-color: #0a0a0a !important; }
            .bg-gray-100 { background-color: #262626 !important; }
            .text-gray-800, .text-gray-900 { color: #e5e5e5 !important; font-weight: 500 !important; }
            .text-gray-600, .text-gray-700 { color: #a3a3a3 !important; }
            .border-gray-200 { border-color: #404040 !important; }
            .bg-blue-600, .bg-indigo-600 { background-color: #00ff88 !important; color: #000000 !important; }
            .hover\:bg-blue-700:hover, .hover\:bg-indigo-700:hover { background-color: #00cc6a !important; }
            h1, h2, h3, h4, h5, h6 { color: #ffffff !important; font-weight: 600 !important; }
            svg.lucide, .lucide { color: #00ff88 !important; stroke: #00ff88 !important; }
            label, p { color: #d4d4d4 !important; font-weight: 500 !important; }
          `;
          break;
      }

      if (themeCSS) {
        style.textContent = themeCSS;
        document.head.appendChild(style);
      }
    } else {
      // Tema light ou nenhum tema - manter original
      console.log("🎨 Mantendo tema original da calculadora");

      // Remover qualquer background forçado para voltar ao original
      body.style.removeProperty("background-color");
      body.style.removeProperty("background");
    }

    console.log("✅ Tema aplicado!");
  }, [settings?.theme, settings?.customColors]);
};
