import React, { useState } from "react";
import {
  X,
  Settings as SettingsIcon,
  User,
  History,
  Database,
  Palette,
  Layout,
  Type,
  Zap,
} from "lucide-react";
import { themes } from "../themes/themes";
// import ColorCustomizer from "./ColorCustomizer"; // Comentei, pois pode não existir

const SettingsModal = ({
  isOpen,
  onClose,
  settings,
  updateSetting,
  profiles,
  history,
  onExportData,
  onImportData,
  onClearAllData,
  // As props customColors, onPrimaryColorChange, onAccentColorChange, onResetColors
  // são passadas do ProgressionCalculator.jsx para cá.
  // Elas são usadas para controlar a lógica de cores.
  onPrimaryColorChange,
  onAccentColorChange,
  onResetColors,
  t, // Função de tradução
}) => {
  const [activeTab, setActiveTab] = useState("general");
  const [importText, setImportText] = useState("");

  if (!isOpen) return null;

  // Determina se o tema atual é escuro para aplicar classes CSS
  const isDark = settings.theme !== "light" && settings.theme !== "retro";

  // Definição das abas do modal de configurações
  const tabs = [
    { id: "general", label: t("settings.general"), icon: SettingsIcon },
    { id: "appearance", label: t("settings.appearance"), icon: Palette },
    { id: "layout", label: t("settings.layout"), icon: Layout },
    { id: "profiles", label: t("settings.profiles"), icon: User },
    { id: "history", label: t("settings.history"), icon: History },
    { id: "data", label: t("settings.data"), icon: Database },
  ];

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div
        className={`${
          isDark ? "bg-gray-800" : "bg-white"
        } rounded-xl shadow-2xl w-full max-w-4xl max-h-[90vh] overflow-hidden`}
      >
        {/* Header do Modal */}
        <div
          className={`flex items-center justify-between p-6 border-b ${
            isDark ? "border-gray-700" : "border-gray-200"
          }`}
        >
          <h2
            className={`text-2xl font-bold ${
              isDark ? "text-white" : "text-gray-800"
            }`}
          >
            {t("settings.title")}
          </h2>
          <button
            onClick={onClose}
            className={`p-2 rounded-lg transition-colors ${
              isDark
                ? "hover:bg-gray-700 text-gray-400"
                : "hover:bg-gray-100 text-gray-600"
            }`}
          >
            <X className="w-5 h-5" />
          </button>
        </div>

        <div className="flex h-[calc(90vh-120px)]">
          {/* Sidebar de Navegação */}
          <div
            className={`w-64 border-r ${
              isDark
                ? "border-gray-700 bg-gray-900"
                : "border-gray-200 bg-gray-50"
            } p-4`}
          >
            <nav className="space-y-2">
              {tabs.map((tab) => {
                const Icon = tab.icon;
                return (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`w-full flex items-center px-3 py-2 rounded-lg text-left transition-colors ${
                      activeTab === tab.id
                        ? isDark
                          ? "bg-gray-700 text-white"
                          : "bg-blue-100 text-blue-700"
                        : isDark
                        ? "text-gray-300 hover:bg-gray-800"
                        : "text-gray-600 hover:bg-gray-100"
                    }`}
                  >
                    <Icon className="w-4 h-4 mr-3" />
                    {tab.label}
                  </button>
                );
              })}
            </nav>
          </div>

          {/* Conteúdo das Abas */}
          <div className="flex-1 p-6 overflow-y-auto">
            {/* Aba: Geral */}
            {activeTab === "general" && (
              <div className="space-y-6">
                <h3
                  className={`text-xl font-semibold ${
                    isDark ? "text-white" : "text-gray-800"
                  }`}
                >
                  {t("settings.generalSettings")}
                </h3>

                {/* Seleção de Idioma */}
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    } mb-2`}
                  >
                    {t("settings.language")}
                  </label>
                  <select
                    value={settings.language || "auto"}
                    onChange={(e) => updateSetting("language", e.target.value)}
                    className={`w-full px-3 py-2 border rounded-lg ${
                      isDark
                        ? "border-gray-600 bg-gray-700 text-white"
                        : "border-gray-300 bg-white"
                    }`}
                  >
                    <option value="auto">🌐 {t("languageAuto")}</option>
                    <option value="pt">🇧🇷 Português</option>
                    <option value="en">🇺🇸 English</option>
                    <option value="es">🇪🇸 Español</option>
                    <option value="fr">🇫🇷 Français</option>
                    <option value="zh">🇨🇳 中文</option>
                    <option value="ja">🇯🇵 日本語</option>
                    <option value="ko">🇰🇷 한국어</option>
                  </select>
                </div>

                {/* Opção: Mostrar Imagem */}
                <div className="flex items-center justify-between">
                  <span
                    className={`text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    {t("settings.showImage")}
                  </span>
                  <label className="relative inline-flex items-center cursor-pointer">
                    <input
                      type="checkbox"
                      checked={settings.showImage !== false}
                      onChange={(e) =>
                        updateSetting("showImage", e.target.checked)
                      }
                      className="sr-only peer"
                    />
                    <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
                  </label>
                </div>

                {/* Opção: Salvamento Automático */}
                <div className="flex items-center justify-between">
                  <span
                    className={`text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    {t("settings.autoSave")}
                  </span>
                  <label className="relative inline-flex items-center cursor-pointer">
                    <input
                      type="checkbox"
                      checked={settings.autoSave !== false}
                      onChange={(e) =>
                        updateSetting("autoSave", e.target.checked)
                      }
                      className="sr-only peer"
                    />
                    <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
                  </label>
                </div>
              </div>
            )}

            {/* Aba: Aparência */}
            {activeTab === "appearance" && (
              <div className="space-y-6">
                <h3
                  className={`text-xl font-semibold ${
                    isDark ? "text-white" : "text-gray-800"
                  }`}
                >
                  {t("settings.appearance")}
                </h3>

                {/* Seleção de Tema */}
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    } mb-3`}
                  >
                    {t("settings.theme")}
                  </label>
                  <div className="grid grid-cols-2 sm:grid-cols-4 gap-3">
                    {Object.entries(themes).map(([key, theme]) => (
                      <button
                        key={key}
                        onClick={() => updateSetting("theme", key)}
                        className={`
                          relative p-3 rounded-lg border-2 transition-all duration-200 text-xs font-medium hover:scale-105
                          ${
                            settings.theme === key
                              ? "border-blue-500 ring-2 ring-blue-200 shadow-lg"
                              : isDark
                              ? "border-gray-600 hover:border-gray-500"
                              : "border-gray-300 hover:border-gray-400"
                          }
                        `}
                      >
                        {/* Preview do tema */}
                        <div
                          className={`w-full h-8 rounded mb-2 ${
                            theme.bg.includes("gradient")
                              ? theme.bg
                              : `${theme.card} ${theme.border} border`
                          }`}
                        ></div>
                        <span
                          className={isDark ? "text-gray-300" : "text-gray-700"}
                        >
                          {theme.name}
                        </span>
                        {settings.theme === key && (
                          <div className="absolute -top-1 -right-1 w-5 h-5 bg-blue-500 rounded-full flex items-center justify-center">
                            <span className="text-white text-xs">✓</span>
                          </div>
                        )}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Seção de Cores Personalizadas */}
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    } mb-3`}
                  >
                    {t("settings.customColors") || "Cores Personalizadas"}
                  </label>

                  <div className="space-y-4">
                    {/* Input de Cor Primária */}
                    <div className="flex items-center justify-between">
                      <span
                        className={`text-sm ${
                          isDark ? "text-gray-300" : "text-gray-700"
                        }`}
                      >
                        {t("settings.primaryColor") || "Cor Primária"}
                      </span>
                      <div className="flex items-center space-x-2">
                        <input
                          type="color"
                          value={settings.customColors?.primary || "#6366f1"}
                          onChange={(e) => {
                            onPrimaryColorChange &&
                              onPrimaryColorChange(e.target.value);
                          }}
                          className="w-10 h-10 rounded-lg border-2 border-gray-300 cursor-pointer"
                        />
                        <span
                          className={`text-xs ${
                            isDark ? "text-gray-400" : "text-gray-600"
                          }`}
                        >
                          {settings.customColors?.primary || "#6366f1"}
                        </span>
                      </div>
                    </div>

                    {/* Input de Cor de Destaque (Accent) */}
                    <div className="flex items-center justify-between">
                      <span
                        className={`text-sm ${
                          isDark ? "text-gray-300" : "text-gray-700"
                        }`}
                      >
                        {t("settings.accentColor") || "Cor de Destaque"}
                      </span>
                      <div className="flex items-center space-x-2">
                        <input
                          type="color"
                          value={settings.customColors?.accent || "#8b5cf6"}
                          onChange={(e) => {
                            onAccentColorChange &&
                              onAccentColorChange(e.target.value);
                          }}
                          className="w-10 h-10 rounded-lg border-2 border-gray-300 cursor-pointer"
                        />
                        <span
                          className={`text-xs ${
                            isDark ? "text-gray-400" : "text-gray-600"
                          }`}
                        >
                          {settings.customColors?.accent || "#8b5cf6"}
                        </span>
                      </div>
                    </div>

                    {/* Botão para Resetar Cores */}
                    <button
                      onClick={() => {
                        onResetColors && onResetColors();
                      }}
                      className={`w-full px-3 py-2 text-sm rounded-lg border transition-colors ${
                        isDark
                          ? "border-gray-600 text-gray-300 hover:bg-gray-700"
                          : "border-gray-300 text-gray-700 hover:bg-gray-50"
                      }`}
                    >
                      {t("settings.resetColors") || "Resetar Cores"}
                    </button>
                  </div>
                </div>

                {/* Se você tiver um componente ColorCustomizer, pode descomentar e usá-lo aqui */}
                {/* 
                {ColorCustomizer && (
                  <ColorCustomizer
                    settings={settings}
                    updateSetting={updateSetting}
                    t={t}
                    isDark={isDark}
                  />
                )}
                */}
              </div>
            )}

            {/* Aba: Layout */}
            {activeTab === "layout" && (
              <div className="space-y-6">
                <h3
                  className={`text-xl font-semibold ${
                    isDark ? "text-white" : "text-gray-800"
                  }`}
                >
                  {t("settings.layout")}
                </h3>

                {/* Tamanho da Fonte */}
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    } mb-2`}
                  >
                    <Type className="w-4 h-4 inline mr-2" />
                    {t("settings.fontSize") || "Tamanho da Fonte"}
                  </label>
                  <select
                    value={settings.fontSize || "normal"}
                    onChange={(e) => updateSetting("fontSize", e.target.value)}
                    className={`w-full px-3 py-2 border rounded-lg ${
                      isDark
                        ? "border-gray-600 bg-gray-700 text-white"
                        : "border-gray-300 bg-white"
                    }`}
                  >
                    <option value="small">
                      {t("settings.fontSmall") || "Pequena"}
                    </option>
                    <option value="normal">
                      {t("settings.fontNormal") || "Normal"}
                    </option>
                    <option value="large">
                      {t("settings.fontLarge") || "Grande"}
                    </option>
                    <option value="xlarge">
                      {t("settings.fontXLarge") || "Extra Grande"}
                    </option>
                  </select>
                </div>

                {/* Densidade da Interface */}
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    } mb-2`}
                  >
                    <Layout className="w-4 h-4 inline mr-2" />
                    {t("settings.interfaceDensity") || "Densidade da Interface"}
                  </label>
                  <select
                    value={settings.density || "normal"}
                    onChange={(e) => updateSetting("density", e.target.value)}
                    className={`w-full px-3 py-2 border rounded-lg ${
                      isDark
                        ? "border-gray-600 bg-gray-700 text-white"
                        : "border-gray-300 bg-white"
                    }`}
                  >
                    <option value="compact">
                      {t("settings.densityCompact") || "Compacta"}
                    </option>
                    <option value="normal">
                      {t("settings.densityNormal") || "Normal"}
                    </option>
                    <option value="comfortable">
                      {t("settings.densityComfortable") || "Confortável"}
                    </option>
                  </select>
                </div>

                {/* Animações */}
                <div className="flex items-center justify-between">
                  <span
                    className={`text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    }`}
                  >
                    <Zap className="w-4 h-4 inline mr-2" />
                    {t("settings.animations") || "Animações"}
                  </span>
                  <label className="relative inline-flex items-center cursor-pointer">
                    <input
                      type="checkbox"
                      checked={settings.animations !== false}
                      onChange={(e) =>
                        updateSetting("animations", e.target.checked)
                      }
                      className="sr-only peer"
                    />
                    <div className="w-11 h-6 bg-gray-200 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 rounded-full peer peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-blue-600"></div>
                  </label>
                </div>

                {/* Raio da Borda (Border Radius) */}
                <div>
                  <label
                    className={`block text-sm font-medium ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    } mb-2`}
                  >
                    {t("settings.borderRadius") || "Raio da Borda"}:{" "}
                    {settings.borderRadius || 8}px
                  </label>
                  <input
                    type="range"
                    min="0"
                    max="24"
                    value={settings.borderRadius || 8}
                    onChange={(e) =>
                      updateSetting("borderRadius", parseInt(e.target.value))
                    }
                    className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer"
                  />
                  <div className="flex justify-between text-xs text-gray-500 mt-1">
                    <span>{t("settings.borderSquare") || "Quadrado"}</span>
                    <span>{t("settings.borderRounded") || "Arredondado"}</span>
                  </div>
                </div>
              </div>
            )}

            {/* Aba: Perfis */}
            {activeTab === "profiles" && (
              <div className="space-y-6">
                <h3
                  className={`text-xl font-semibold ${
                    isDark ? "text-white" : "text-gray-800"
                  }`}
                >
                  {t("settings.savedProfiles")}
                </h3>
                {profiles.savedProfiles && profiles.savedProfiles.length > 0 ? (
                  <div className="space-y-3">
                    {profiles.savedProfiles.map((profile) => (
                      <div
                        key={profile.id}
                        className={`p-4 rounded-lg border ${
                          isDark
                            ? "border-gray-600 bg-gray-700"
                            : "border-gray-200 bg-gray-50"
                        }`}
                      >
                        <div className="flex items-center justify-between">
                          <div>
                            <h4
                              className={`font-medium ${
                                isDark ? "text-white" : "text-gray-800"
                              }`}
                            >
                              {profile.name}
                            </h4>
                            <p
                              className={`text-sm ${
                                isDark ? "text-gray-400" : "text-gray-600"
                              }`}
                            >
                              {t("settings.created")}:{" "}
                              {new Date(profile.createdAt).toLocaleDateString()}
                            </p>
                          </div>
                          <div className="flex space-x-2">
                            <button
                              onClick={() => profiles.loadProfile(profile)}
                              className="px-3 py-1 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm"
                            >
                              {t("settings.load")}
                            </button>
                            <button
                              onClick={() =>
                                profiles.deleteProfile &&
                                profiles.deleteProfile(profile.id)
                              }
                              className="px-3 py-1 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors text-sm"
                            >
                              {t("settings.delete") || "Excluir"}
                            </button>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <p
                    className={`text-center py-8 ${
                      isDark ? "text-gray-400" : "text-gray-600"
                    }`}
                  >
                    {t("settings.noProfiles")}
                  </p>
                )}
              </div>
            )}

            {/* Aba: Histórico */}
            {activeTab === "history" && (
              <div className="space-y-6">
                <div className="flex items-center justify-between">
                  <h3
                    className={`text-xl font-semibold ${
                      isDark ? "text-white" : "text-gray-800"
                    }`}
                  >
                    {t("settings.calculationHistory")}
                  </h3>
                  {history.history && history.history.length > 0 && (
                    <button
                      onClick={history.clearHistory}
                      className="px-3 py-1 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors text-sm"
                    >
                      {t("settings.clearHistory")}
                    </button>
                  )}
                </div>
                {history.history && history.history.length > 0 ? (
                  <div className="space-y-3 max-h-96 overflow-y-auto">
                    {history.history
                      .slice()
                      .reverse()
                      .map((item, index) => (
                        <div
                          key={index}
                          className={`p-4 rounded-lg border ${
                            isDark
                              ? "border-gray-600 bg-gray-700"
                              : "border-gray-200 bg-gray-50"
                          }`}
                        >
                          <div className="flex items-center justify-between mb-2">
                            <span
                              className={`text-sm ${
                                isDark ? "text-gray-400" : "text-gray-600"
                              }`}
                            >
                              {new Date(item.timestamp).toLocaleString()}
                            </span>
                          </div>
                          <p
                            className={`text-sm ${
                              isDark ? "text-white" : "text-gray-800"
                            }`}
                          >
                            {t("settings.result")}:{" "}
                            {item.result?.timeFormatted || "N/A"}
                          </p>
                        </div>
                      ))}
                  </div>
                ) : (
                  <p
                    className={`text-center py-8 ${
                      isDark ? "text-gray-400" : "text-gray-600"
                    }`}
                  >
                    {t("settings.noHistory")}
                  </p>
                )}
              </div>
            )}

            {/* Aba: Dados */}
            {activeTab === "data" && (
              <div className="space-y-6">
                <h3
                  className={`text-xl font-semibold ${
                    isDark ? "text-white" : "text-gray-800"
                  }`}
                >
                  {t("settings.dataManagement")}
                </h3>

                {/* Exportar Dados */}
                <div
                  className={`p-4 rounded-lg border ${
                    isDark
                      ? "border-gray-600 bg-gray-700"
                      : "border-gray-200 bg-gray-50"
                  }`}
                >
                  <h4
                    className={`font-medium mb-2 ${
                      isDark ? "text-white" : "text-gray-800"
                    }`}
                  >
                    {t("settings.exportData")}
                  </h4>
                  <p
                    className={`text-sm mb-3 ${
                      isDark ? "text-gray-400" : "text-gray-600"
                    }`}
                  >
                    {t("settings.exportDescription")}
                  </p>
                  <button
                    onClick={onExportData}
                    className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
                  >
                    {t("settings.export")}
                  </button>
                </div>

                {/* Importar Dados */}
                <div
                  className={`p-4 rounded-lg border ${
                    isDark
                      ? "border-gray-600 bg-gray-700"
                      : "border-gray-200 bg-gray-50"
                  }`}
                >
                  <h4
                    className={`font-medium mb-2 ${
                      isDark ? "text-white" : "text-gray-800"
                    }`}
                  >
                    {t("settings.importData")}
                  </h4>
                  <p
                    className={`text-sm mb-3 ${
                      isDark ? "text-gray-400" : "text-gray-600"
                    }`}
                  >
                    {t("settings.importDescription")}
                  </p>
                  <textarea
                    value={importText}
                    onChange={(e) => setImportText(e.target.value)}
                    placeholder={t("settings.pasteDataHere")}
                    className={`w-full h-32 px-3 py-2 border rounded-lg mb-3 ${
                      isDark
                        ? "border-gray-600 bg-gray-800 text-white"
                        : "border-gray-300 bg-white"
                    }`}
                  />
                  <button
                    onClick={() => {
                      try {
                        const data = JSON.parse(importText);
                        onImportData(data);
                        setImportText("");
                      } catch (error) {
                        alert(t("settings.importError"));
                      }
                    }}
                    disabled={!importText.trim()}
                    className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {t("settings.import")}
                  </button>
                </div>

                {/* Limpar Todos os Dados */}
                <div
                  className={`p-4 rounded-lg border border-red-300 ${
                    isDark ? "bg-red-900/20" : "bg-red-50"
                  }`}
                >
                  <h4
                    className={`font-medium mb-2 ${
                      isDark ? "text-red-400" : "text-red-800"
                    }`}
                  >
                    {t("settings.clearAllData")}
                  </h4>
                  <p
                    className={`text-sm mb-3 ${
                      isDark ? "text-red-300" : "text-red-700"
                    }`}
                  >
                    {t("settings.clearDataWarning")}
                  </p>
                  <button
                    onClick={() => {
                      if (window.confirm(t("settings.confirmClearData"))) {
                        onClearAllData();
                      }
                    }}
                    className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
                  >
                    {t("settings.clearData")}
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SettingsModal;
