import React, { useState, useRef } from "react";
import {
  Calculator,
  TrendingUp,
  Clock,
  Target,
  AlertCircle,
  CheckCircle,
  Coins,
  Filter,
} from "lucide-react";
// Certifique-se de que o caminho para analyzeMinerWithFormulas está correto
import { analyzeMinerWithFormulas } from "../utils/minerEfficiencyCalculator";

const MinerEfficiencyAnalysis = ({
  settings,
  updateSetting,
  t,
  isDark,
  textClass,
  cardClass,
  bgClass,
  profiles,
  history,
  efficiencyData, // NOVO: Dados de eficiência do pai
  setEfficiencyData, // NOVO: Setter para os dados de eficiência do pai
  updateCurrentProfileInParent, // NOVO: Função para auto-save no pai
}) => {
  // 🛡️ PROTEÇÃO DEFENSIVA: Garantir que efficiencyData sempre seja um objeto válido
  const safeEfficiencyData = efficiencyData || {
    minerName: "",
    currentLevel: "",
    currentCost: "",
    goldGenerationPer5s: "",
    timeLeftSeconds: "",
    timeCalculatorGenerationRate: "",
    timeCalculatorTargetAmount: "",
  };

  // Estados locais (mantidos)
  const [analysis, setAnalysis] = useState(null);
  const [timeResult, setTimeResult] = useState(null);
  const [showDetailedAnalysis, setShowDetailedAnalysis] = useState(false);
  const [error, setError] = useState(null);
  const [levelFilter, setLevelFilter] = useState("milestones");

  // 🔧 NOVO: Ref para controlar o timeout do auto-save
  const autoSaveTimeoutRef = useRef(null);

  // �� FUNÇÃO MELHORADA: com debounce mais robusto
  const updateEfficiencyDataAndSave = (field, value) => {
    if (!setEfficiencyData) {
      console.warn("setEfficiencyData não está disponível");
      return;
    }

    const newData = { ...safeEfficiencyData, [field]: value };
    setEfficiencyData(newData); // Atualiza o estado no componente pai IMEDIATAMENTE

    // Cancela o timeout anterior se existir
    if (autoSaveTimeoutRef.current) {
      clearTimeout(autoSaveTimeoutRef.current);
    }

    // Aciona o auto-save no componente pai após um delay (apenas se auto-save estiver ativo)
    if (settings.autoSave && updateCurrentProfileInParent) {
      autoSaveTimeoutRef.current = setTimeout(() => {
        updateCurrentProfileInParent(newData);
        autoSaveTimeoutRef.current = null; // Limpa a referência
      }, 1000); // Aumentei para 1 segundo para dar mais tempo ao usuário
    }
  };

  // 🔢 SEQUÊNCIA CORRETA DE NÍVEIS (igual ao ProgressionCalculator)
  const levels = [
    "B",
    "T",
    "a",
    "b",
    "c",
    "d",
    "e",
    "f",
    "g",
    "h",
    "i",
    "j",
    "k",
    "l",
    "m",
    "n",
    "o",
    "p",
    "q",
    "r",
    "s",
    "t",
    "u",
    "v",
    "w",
    "x",
    "y",
    "z",
    "aa",
    "ab",
    "ac",
    "ad",
    "ae",
    "af",
    "ag",
    "ah",
    "ai",
    "aj",
    "ak",
    "al",
    "am",
    "an",
    "ao",
    "ap",
    "aq",
    "ar",
    "as",
    "at",
    "au",
    "av",
    "aw",
    "ax",
    "ay",
    "az",
  ];

  // 🎨 FUNÇÕES DE CORES E ÍCONES
  const getRecommendationColor = (recommendation) => {
    if (recommendation.includes("IDEAL"))
      return isDark
        ? "bg-purple-900/30 border-purple-700 text-purple-400"
        : "bg-purple-100 border-purple-300 text-purple-800";
    if (recommendation.includes("EXCELENTE"))
      return isDark
        ? "bg-green-900/30 border-green-700 text-green-400"
        : "bg-green-100 border-green-300 text-green-800";
    if (recommendation.includes("BOM"))
      return isDark
        ? "bg-blue-900/30 border-blue-700 text-blue-400"
        : "bg-blue-100 border-blue-300 text-blue-800";
    if (recommendation.includes("MODERADO"))
      return isDark
        ? "bg-yellow-900/30 border-yellow-700 text-yellow-400"
        : "bg-yellow-100 border-yellow-300 text-yellow-800";
    if (recommendation.includes("LENTO"))
      return isDark
        ? "bg-red-900/30 border-red-700 text-red-400"
        : "bg-red-100 border-red-300 text-red-800";

    return isDark
      ? "bg-gray-900/30 border-gray-700 text-gray-400"
      : "bg-gray-100 border-gray-300 text-gray-800";
  };

  const getEfficiencyIcon = (recommendation) => {
    if (
      recommendation.includes("IDEAL") ||
      recommendation.includes("EXCELENTE") ||
      recommendation.includes("BOM")
    ) {
      return <CheckCircle className="w-5 h-5" />;
    }
    return <AlertCircle className="w-5 h-5" />;
  };

  const getEfficiencyColor = (efficiency) => {
    switch (efficiency) {
      case t("efficiency.veryFast"):
        return "text-green-600";
      case t("efficiency.fast"):
        return "text-blue-600";
      case t("efficiency.moderate"):
        return "text-yellow-600";
      case t("efficiency.slow"):
        return "text-red-600";
      default:
        return "text-gray-600";
    }
  };

  const getRoiColor = (roiValue) => {
    const roi = parseFloat(roiValue);

    if (roi >= 50) {
      // ROI Excelente (≥50%)
      return isDark ? "text-green-400" : "text-green-600";
    } else if (roi >= 20) {
      // ROI Bom (20-49%)
      return isDark ? "text-blue-400" : "text-blue-600";
    } else if (roi >= 10) {
      // ROI Moderado (10-19%)
      return isDark ? "text-yellow-400" : "text-yellow-600";
    } else if (roi >= 5) {
      // ROI Baixo (5-9%)
      return isDark ? "text-orange-400" : "text-orange-600";
    } else {
      // ROI Muito Baixo (<5%)
      return isDark ? "text-red-400" : "text-red-600";
    }
  };

  const getRoiIcon = (roiValue) => {
    const roi = parseFloat(roiValue);

    if (roi >= 50) return "🚀"; // Excelente
    if (roi >= 20) return "⭐"; // Bom
    if (roi >= 10) return "✅"; // Moderado
    if (roi >= 5) return "⚠️"; // Baixo
    return "❌"; // Muito Baixo
  };

  // 🔢 Função para converter notação usando a sequência correta
  const convertToBase = (amount, level) => {
    const levelIndex = levels.indexOf(level);
    if (levelIndex === -1) return 0;
    return amount * Math.pow(1000, levelIndex);
  };

  const convertFromBase = (baseAmount, targetLevel) => {
    const levelIndex = levels.indexOf(targetLevel);
    if (levelIndex === -1) return 0;
    return baseAmount / Math.pow(1000, levelIndex);
  };

  // Função para converter notação científica (CORRIGIDA)
  const convertNotation = (value) => {
    if (!value || typeof value !== "string") return 0;

    // Remover espaços
    const cleanValue = value.trim();

    // Tentar encontrar uma notação no final da string
    for (const level of levels) {
      if (cleanValue.toLowerCase().endsWith(level.toLowerCase())) {
        const numberPart = cleanValue.slice(0, -level.length);
        const number = parseFloat(numberPart);
        if (!isNaN(number)) {
          return convertToBase(number, level);
        }
      }
    }

    // Se não encontrou notação, tentar converter como número normal
    const number = parseFloat(cleanValue);
    return isNaN(number) ? 0 : number;
  };

  // Função para formatar tempo (AGORA USA 't' PARA TRADUÇÕES)
  const formatTime = (seconds) => {
    if (seconds === Infinity || isNaN(seconds) || seconds < 0)
      return t("timeUnits.infinite"); // Chave de tradução para "Infinito"

    const years = Math.floor(seconds / (365.25 * 24 * 3600));
    seconds %= 365.25 * 24 * 3600;
    const days = Math.floor(seconds / (24 * 3600));
    seconds %= 24 * 3600;
    const hours = Math.floor(seconds / 3600);
    seconds %= 3600;
    const minutes = Math.floor(seconds / 60);
    seconds %= 60;
    const remainingSeconds = Math.round(seconds);

    const parts = [];
    if (years > 0)
      parts.push(
        `${years} ${years > 1 ? t("timeUnits.years") : t("timeUnits.year")}`
      );
    if (days > 0)
      parts.push(
        `${days} ${days > 1 ? t("timeUnits.days") : t("timeUnits.day")}`
      );
    if (hours > 0)
      parts.push(
        `${hours} ${hours > 1 ? t("timeUnits.hours") : t("timeUnits.hour")}`
      );
    if (minutes > 0)
      parts.push(
        `${minutes} ${
          minutes > 1 ? t("timeUnits.minutes") : t("timeUnits.minute")
        }`
      );
    if (remainingSeconds > 0 || parts.length === 0)
      parts.push(
        `${remainingSeconds} ${
          remainingSeconds > 1 ? t("timeUnits.seconds") : t("timeUnits.second")
        }`
      );

    return parts.join(", ");
  };

  // Função para formatar números grandes (CORRIGIDA)
  const formatToNotation = (num) => {
    if (num === 0) return "0";

    // Encontrar o melhor nível para exibição
    for (let i = levels.length - 1; i >= 0; i--) {
      const levelValue = Math.pow(1000, i);
      if (num >= levelValue) {
        const convertedValue = num / levelValue;
        return convertedValue.toFixed(2) + levels[i];
      }
    }

    return num.toLocaleString();
  };

  // 🆕 Função para filtrar a análise baseada na seleção
  const getFilteredAnalysis = () => {
    if (!analysis?.optimalAnalysis?.analysis) return [];

    const fullAnalysis = analysis.optimalAnalysis.analysis;

    switch (levelFilter) {
      case "milestones":
        return fullAnalysis.filter((item) => item.milestone || item.isOptimal);
      case "first100":
        return fullAnalysis.slice(0, 100);
      case "every10":
        return fullAnalysis.filter(
          (item, index) =>
            (index + 1) % 10 === 0 || item.isOptimal || item.milestone
        );
      case "every50":
        return fullAnalysis.filter(
          (item, index) =>
            (index + 1) % 50 === 0 || item.isOptimal || item.milestone
        );
      case "all":
      default:
        return fullAnalysis;
    }
  };

  // Função para calcular tempo para objetivo
  const calculateTimeToTarget = () => {
    setError(null); // Limpa erros anteriores
    try {
      // 🔧 Usa efficiencyData para obter os valores
      const generationRate = convertNotation(
        efficiencyData.timeCalculatorGenerationRate
      );
      const targetAmount = convertNotation(
        efficiencyData.timeCalculatorTargetAmount
      );

      if (generationRate <= 0) {
        setTimeResult({ error: t("errorInvalidValues") });
        return;
      }

      if (targetAmount <= 0) {
        setTimeResult({ error: t("errorInvalidValues") });
        return;
      }

      // Converter taxa de "por 5s" para "por segundo"
      const ratePerSecond = generationRate / 5;

      // Calcular tempo em segundos
      const timeInSeconds = targetAmount / ratePerSecond;

      // Calcular data estimada de conclusão
      const completionDate = new Date(Date.now() + timeInSeconds * 1000);

      setTimeResult({
        timeInSeconds,
        timeFormatted: formatTime(timeInSeconds),
        ratePerSecond: formatToNotation(ratePerSecond),
        targetFormatted: formatToNotation(targetAmount),
        generationFormatted: formatToNotation(generationRate),
        estimatedCompletion: completionDate.toLocaleString(
          settings.language || "pt-BR"
        ),
        efficiency:
          timeInSeconds < 3600
            ? t("efficiency.veryFast")
            : timeInSeconds < 86400
            ? t("efficiency.fast")
            : timeInSeconds < 604800
            ? t("efficiency.moderate")
            : t("efficiency.slow"),
      });
    } catch (err) {
      setTimeResult({
        error: t("errorGeneral"),
      });
    }
  };

  // Funções da análise de eficiência (mantidas)
  const handleAnalyze = () => {
    setError(null);
    try {
      // 🔧 Usa efficiencyData para obter os valores
      const data = {
        name: efficiencyData.minerName || t("minerName"),
        currentLevel: parseInt(efficiencyData.currentLevel) || 0,
        currentCost: efficiencyData.currentCost || "0",
        goldGenerationPer5s: efficiencyData.goldGenerationPer5s || "0",
        timeLeftSeconds: efficiencyData.timeLeftSeconds
          ? parseInt(efficiencyData.timeLeftSeconds)
          : null,
      };

      if (data.currentLevel <= 0) {
        throw new Error(t("error.levelPositive"));
      }
      if (!data.currentCost || data.currentCost.trim() === "0") {
        throw new Error(t("error.costNotEmpty"));
      }
      if (
        !data.goldGenerationPer5s ||
        data.goldGenerationPer5s.trim() === "0"
      ) {
        throw new Error(t("error.generationNotEmpty"));
      }

      const result = analyzeMinerWithFormulas(data);
      if (result.error) {
        setError(result.error);
        setAnalysis(null);
      } else {
        setAnalysis(result);
      }
    } catch (err) {
      console.error("Erro na análise:", err);
      setError(err.message || t("errorGeneral"));
      setAnalysis(null);
    }
  };

  return (
    <div className="max-w-6xl mx-auto p-6 space-y-8">
      {/* 🎯 Agradecimento */}
      <p
        className={`text-xs ${
          isDark ? "text-gray-400" : "text-gray-500"
        } text-center mb-4 italic`}
      >
        🙏 {t("minerEfficiency.acknowledgement.masNotProdFormulas")}
      </p>

      {/* ===== NOVA SEÇÃO: Calculadora de Tempo para Objetivo ===== */}
      <div className={`${cardClass} rounded-lg shadow-lg p-6`}>
        <div className="flex items-center gap-3 mb-6">
          <Clock className="w-8 h-8 text-green-600" />
          <h2 className={`text-2xl font-bold ${textClass}`}>
            {t("timeCalculator.title")}
          </h2>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
          <div>
            <label className={`block text-sm font-medium ${textClass} mb-2`}>
              {t("timeCalculator.generationRate")} (Ex: 3.6aq/5s)
            </label>
            <input
              type="text"
              // 🔧 Usa efficiencyData e updateEfficiencyDataAndSave
              value={efficiencyData.timeCalculatorGenerationRate}
              onChange={(e) =>
                updateEfficiencyDataAndSave(
                  "timeCalculatorGenerationRate",
                  e.target.value
                )
              }
              placeholder="Ex: 3.6aq"
              className={`w-full px-3 py-2 border ${
                isDark
                  ? "border-gray-600 bg-gray-800 text-white"
                  : "border-gray-300 bg-white"
              } rounded-md focus:outline-none focus:ring-2 focus:ring-green-500`}
            />
            <p
              className={`text-xs ${
                isDark ? "text-gray-400" : "text-gray-500"
              } mt-1`}
            >
              {t("timeCalculator.generationRateDescription")}
            </p>
          </div>

          <div>
            <label className={`block text-sm font-medium ${textClass} mb-2`}>
              {t("timeCalculator.targetAmount")} (Ex: 4.88ar)
            </label>
            <input
              type="text"
              // 🔧 Usa efficiencyData e updateEfficiencyDataAndSave
              value={efficiencyData.timeCalculatorTargetAmount}
              onChange={(e) =>
                updateEfficiencyDataAndSave(
                  "timeCalculatorTargetAmount",
                  e.target.value
                )
              }
              placeholder="Ex: 4.88ar"
              className={`w-full px-3 py-2 border ${
                isDark
                  ? "border-gray-600 bg-gray-800 text-white"
                  : "border-gray-300 bg-white"
              } rounded-md focus:outline-none focus:ring-2 focus:ring-green-500`}
            />
            <p
              className={`text-xs ${
                isDark ? "text-gray-400" : "text-gray-500"
              } mt-1`}
            >
              {t("timeCalculator.targetAmountDescription")}
            </p>
          </div>
        </div>

        <div className="flex justify-center mb-6">
          <button
            onClick={calculateTimeToTarget}
            className="flex items-center gap-2 px-6 py-3 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors"
          >
            <Clock className="w-5 h-5" />
            {t("timeCalculator.calculateButton")}
          </button>
        </div>

        {/* Resultado do Tempo para Objetivo */}
        {timeResult && (
          <div
            className={`rounded-lg p-6 border ${
              timeResult.error
                ? isDark
                  ? "bg-red-900/30 border-red-700"
                  : "bg-red-50 border-red-200"
                : isDark
                ? "bg-green-900/30 border-green-700"
                : "bg-green-50 border-green-200"
            }`}
          >
            {timeResult.error ? (
              <div className={isDark ? "text-red-400" : "text-red-700"}>
                <h3 className="font-semibold mb-2">❌ {t("error")}</h3>
                <p>{timeResult.error}</p>
              </div>
            ) : (
              <div>
                <h3
                  className={`text-xl font-semibold ${
                    isDark ? "text-green-400" : "text-green-800"
                  } mb-4`}
                >
                  ⏰ {t("timeCalculator.resultTitle")}
                </h3>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div className="space-y-3">
                    <div
                      className={`${
                        isDark ? "bg-gray-800" : "bg-white"
                      } p-4 rounded-lg`}
                    >
                      <p
                        className={`text-sm ${
                          isDark ? "text-gray-400" : "text-gray-600"
                        }`}
                      >
                        {t("timeCalculator.timeNeeded")}:
                      </p>
                      <p
                        className={`text-2xl font-bold ${
                          isDark ? "text-green-400" : "text-green-600"
                        }`}
                      >
                        {timeResult.timeFormatted}
                      </p>
                    </div>

                    <div
                      className={`${
                        isDark ? "bg-gray-800" : "bg-white"
                      } p-4 rounded-lg`}
                    >
                      <p
                        className={`text-sm ${
                          isDark ? "text-gray-400" : "text-gray-600"
                        }`}
                      >
                        {t("timeCalculator.efficiency")}:
                      </p>
                      <p
                        className={`text-lg font-semibold ${getEfficiencyColor(
                          timeResult.efficiency
                        )}`}
                      >
                        {timeResult.efficiency}
                      </p>
                    </div>
                  </div>

                  <div className="space-y-3">
                    <div
                      className={`${
                        isDark ? "bg-gray-800" : "bg-white"
                      } p-4 rounded-lg`}
                    >
                      <p
                        className={`text-sm ${
                          isDark ? "text-gray-400" : "text-gray-600"
                        }`}
                      >
                        {t("timeCalculator.ratePerSecond")}:
                      </p>
                      <p
                        className={`text-lg font-semibold ${
                          isDark ? "text-blue-400" : "text-blue-600"
                        }`}
                      >
                        {timeResult.ratePerSecond}/s
                      </p>
                    </div>

                    <div
                      className={`${
                        isDark ? "bg-gray-800" : "bg-white"
                      } p-4 rounded-lg`}
                    >
                      <p
                        className={`text-sm ${
                          isDark ? "text-gray-400" : "text-gray-600"
                        }`}
                      >
                        {t("timeCalculator.estimatedCompletion")}:
                      </p>
                      <p
                        className={`text-sm font-medium ${
                          isDark ? "text-gray-300" : "text-gray-700"
                        }`}
                      >
                        {timeResult.estimatedCompletion}
                      </p>
                    </div>
                  </div>
                </div>

                {/* RESUMO CORRIGIDO */}
                <div
                  className={`mt-4 ${
                    isDark ? "bg-gray-800" : "bg-white"
                  } p-4 rounded-lg`}
                >
                  <h4
                    className={`font-semibold ${
                      isDark ? "text-gray-300" : "text-gray-700"
                    } mb-2`}
                  >
                    📊 {t("timeCalculator.summary")}:
                  </h4>
                  <p
                    className={`text-sm ${
                      isDark ? "text-gray-400" : "text-gray-600"
                    }`}
                  >
                    {t("timeCalculator.summaryText")
                      .replace("{0}", timeResult.generationFormatted)
                      .replace("{1}", timeResult.targetFormatted)
                      .replace("{2}", timeResult.timeFormatted)}
                  </p>

                  {/* Informações detalhadas */}
                  <div className="mt-3 grid grid-cols-1 md:grid-cols-3 gap-3 text-xs">
                    <div>
                      <span
                        className={`font-medium ${
                          isDark ? "text-gray-300" : "text-gray-700"
                        }`}
                      >
                        {t("timeCalculator.generationRate")}:
                      </span>
                      <span
                        className={`ml-1 ${
                          isDark ? "text-blue-400" : "text-blue-600"
                        }`}
                      >
                        {timeResult.generationFormatted}/5s
                      </span>
                    </div>
                    <div>
                      <span
                        className={`font-medium ${
                          isDark ? "text-gray-300" : "text-gray-700"
                        }`}
                      >
                        {t("timeCalculator.targetAmount")}:
                      </span>
                      <span
                        className={`ml-1 ${
                          isDark ? "text-green-400" : "text-green-600"
                        }`}
                      >
                        {timeResult.targetFormatted}
                      </span>
                    </div>
                    <div>
                      <span
                        className={`font-medium ${
                          isDark ? "text-gray-300" : "text-gray-700"
                        }`}
                      >
                        {t("timeCalculator.timeNeeded")}:
                      </span>
                      <span
                        className={`ml-1 ${
                          isDark ? "text-purple-400" : "text-purple-600"
                        }`}
                      >
                        {timeResult.timeFormatted}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      {/* ===== SEÇÃO ORIGINAL: Análise de Eficiência ===== */}
      <div className={`${cardClass} rounded-lg shadow-lg p-6`}>
        <div className="flex items-center gap-3 mb-6">
          <Calculator className="w-8 h-8 text-blue-600" />
          <h2 className={`text-2xl font-bold ${textClass}`}>
            {t("efficiencyAnalysis")}
          </h2>
        </div>

        {/* Formulário de Entrada */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
          <div>
            <label className={`block text-sm font-medium ${textClass} mb-2`}>
              {t("minerName")}
            </label>
            <input
              type="text"
              // 🔧 Usa efficiencyData e updateEfficiencyDataAndSave
              value={efficiencyData.minerName}
              onChange={(e) =>
                updateEfficiencyDataAndSave("minerName", e.target.value)
              }
              placeholder="Ex: Koom, Akadi"
              className={`w-full px-3 py-2 border ${
                isDark
                  ? "border-gray-600 bg-gray-800 text-white"
                  : "border-gray-300 bg-white"
              } rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500`}
            />
          </div>

          <div>
            <label className={`block text-sm font-medium ${textClass} mb-2`}>
              {t("currentLevel")}
            </label>
            <input
              type="number"
              // �� Usa efficiencyData e updateEfficiencyDataAndSave
              value={efficiencyData.currentLevel}
              onChange={(e) =>
                updateEfficiencyDataAndSave("currentLevel", e.target.value)
              }
              placeholder="Ex: 12632"
              min="1"
              className={`w-full px-3 py-2 border ${
                isDark
                  ? "border-gray-600 bg-gray-800 text-white"
                  : "border-gray-300 bg-white"
              } rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500`}
            />
          </div>

          <div>
            <label className={`block text-sm font-medium ${textClass} mb-2`}>
              {t("nextUpgradeCost")} (Ex: 87.49ap)
            </label>
            <input
              type="text"
              // 🔧 Usa efficiencyData e updateEfficiencyDataAndSave
              value={efficiencyData.currentCost}
              onChange={(e) =>
                updateEfficiencyDataAndSave("currentCost", e.target.value)
              }
              placeholder="Ex: 87.49ap"
              className={`w-full px-3 py-2 border ${
                isDark
                  ? "border-gray-600 bg-gray-800 text-white"
                  : "border-gray-300 bg-white"
              } rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500`}
            />
          </div>

          <div>
            <label className={`block text-sm font-medium ${textClass} mb-2`}>
              {t("currentGeneration")} (Ex: 26.87ap/5s)
            </label>
            <input
              type="text"
              // �� Usa efficiencyData e updateEfficiencyDataAndSave
              value={efficiencyData.goldGenerationPer5s}
              onChange={(e) =>
                updateEfficiencyDataAndSave(
                  "goldGenerationPer5s",
                  e.target.value
                )
              }
              placeholder="Ex: 26.87ap"
              className={`w-full px-3 py-2 border ${
                isDark
                  ? "border-gray-600 bg-gray-800 text-white"
                  : "border-gray-300 bg-white"
              } rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500`}
            />
          </div>

          <div className="md:col-span-2">
            <label className={`block text-sm font-medium ${textClass} mb-2`}>
              {t("timeLeftOptional")}
            </label>
            <input
              type="number"
              // 🔧 Usa efficiencyData e updateEfficiencyDataAndSave
              value={efficiencyData.timeLeftSeconds}
              onChange={(e) =>
                updateEfficiencyDataAndSave("timeLeftSeconds", e.target.value)
              }
              placeholder="Ex: 3600 (1 hora)"
              min="0"
              className={`w-full px-3 py-2 border ${
                isDark
                  ? "border-gray-600 bg-gray-800 text-white"
                  : "border-gray-300 bg-white"
              } rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500`}
            />
            <p
              className={`text-sm ${
                isDark ? "text-gray-400" : "text-gray-500"
              } mt-1`}
            >
              {t("timeCalculator.optimalTimingDescription")}
            </p>
          </div>
        </div>

        {/* Botão de Análise */}
        <div className="flex justify-center mb-6">
          <button
            onClick={handleAnalyze}
            className="flex items-center gap-2 px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            <TrendingUp className="w-5 h-5" />
            {t("analyze")}
          </button>
        </div>

        {/* Mensagem de Erro */}
        {error && (
          <div
            className={`${
              isDark
                ? "bg-red-900/30 border-red-700 text-red-400"
                : "bg-red-100 border-l-4 border-red-500 text-red-700"
            } p-4 mb-6`}
            role="alert"
          >
            <p className="font-bold">{t("error.analysisTitle")}</p>
            <p>{error}</p>
          </div>
        )}

        {/* Resultados da Análise */}
        {analysis && !error && (
          <div className="space-y-6">
            {/* Resumo Principal */}
            <div
              className={`${
                isDark ? "bg-gray-800" : "bg-gray-50"
              } rounded-lg p-6`}
            >
              <h3 className={`text-xl font-semibold ${textClass} mb-4`}>
                📊 {t("analysisSummaryTitle").replace("{0}", analysis.name)}
              </h3>

              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div
                  className={`${
                    isDark ? "bg-gray-700" : "bg-white"
                  } p-4 rounded-lg shadow-sm`}
                >
                  <div className="flex items-center gap-2 mb-2">
                    <Clock className="w-5 h-5 text-blue-600" />
                    <span className={`font-medium ${textClass}`}>
                      {t("upgradeDuration")}
                    </span>
                  </div>
                  <p
                    className={`text-2xl font-bold ${
                      isDark ? "text-blue-400" : "text-blue-600"
                    }`}
                  >
                    {analysis.upgradeDurationFormatted}
                  </p>
                </div>

                <div
                  className={`${
                    isDark ? "bg-gray-700" : "bg-white"
                  } p-4 rounded-lg shadow-sm`}
                >
                  <div className="flex items-center gap-2 mb-2">
                    <TrendingUp className="w-5 h-5 text-green-600" />
                    <span className={`font-medium ${textClass}`}>
                      {t("roiPerHour")}
                    </span>
                  </div>
                  <p
                    className={`text-2xl font-bold ${getRoiColor(
                      analysis.roiPerHour
                    )}`}
                  >
                    {analysis.roiPerHour}%
                  </p>
                </div>

                <div
                  className={`${
                    isDark ? "bg-gray-700" : "bg-white"
                  } p-4 rounded-lg shadow-sm`}
                >
                  <div className="flex items-center gap-2 mb-2">
                    <Target className="w-5 h-5 text-purple-600" />
                    <span className={`font-medium ${textClass}`}>
                      {t("currentLevel")}
                    </span>
                  </div>
                  <p
                    className={`text-2xl font-bold ${
                      isDark ? "text-purple-400" : "text-purple-600"
                    }`}
                  >
                    {analysis.currentLevel}
                  </p>
                </div>
              </div>

              {/* 🆕 Informações Adicionais de Precisão */}
              <div className="mt-4 grid grid-cols-1 md:grid-cols-2 gap-4">
                <div
                  className={`${
                    isDark ? "bg-blue-900/30" : "bg-blue-50"
                  } p-3 rounded-lg`}
                >
                  <div className="flex items-center gap-2 mb-1">
                    <Target className="w-4 h-4 text-blue-600" />
                    <span
                      className={`text-sm font-medium ${
                        isDark ? "text-blue-400" : "text-blue-800"
                      }`}
                    >
                      {t("precision.ultraHigh")}
                    </span>
                  </div>
                  <p
                    className={`text-xs ${
                      isDark ? "text-blue-300" : "text-blue-600"
                    }`}
                  >
                    {analysis.precision}
                  </p>
                  <p
                    className={`text-xs ${
                      isDark ? "text-blue-400" : "text-blue-500"
                    }`}
                  >
                    {t("precision.maxSupportedLevel").replace(
                      "{0}",
                      analysis.maxSupportedLevel
                    )}
                  </p>
                </div>

                <div
                  className={`${
                    isDark ? "bg-purple-900/30" : "bg-purple-50"
                  } p-3 rounded-lg`}
                >
                  <div className="flex items-center gap-2 mb-1">
                    <Calculator className="w-4 h-4 text-purple-600" />
                    <span
                      className={`text-sm font-medium ${
                        isDark ? "text-purple-400" : "text-purple-800"
                      }`}
                    >
                      {t("minerType")}
                    </span>
                  </div>
                  <p
                    className={`text-xs ${
                      isDark ? "text-purple-300" : "text-purple-600"
                    } capitalize`}
                  >
                    {analysis.minerType}
                  </p>
                  <p
                    className={`text-xs ${
                      isDark ? "text-purple-400" : "text-purple-500"
                    }`}
                  >
                    {t("correctionFactorLabel")}:{" "}
                    {analysis.correctionFactor?.toFixed(3)}
                  </p>
                </div>
              </div>
            </div>

            {/* Recomendação */}
            <div
              className={`p-4 rounded-lg border-2 ${getRecommendationColor(
                analysis.recommendation
              )}`}
            >
              <div className="flex items-center gap-3">
                {getEfficiencyIcon(analysis.recommendation)}
                <div>
                  <h4 className="font-semibold">{t("recommendation")}</h4>
                  <p className="text-sm">
                    {t(analysis.recommendation.toLowerCase())}
                  </p>
                </div>
              </div>
            </div>

            {/* Level Ótimo */}
            {analysis.optimalAnalysis.recommendation && (
              <div
                className={`${
                  isDark ? "bg-purple-900/30" : "bg-purple-50"
                } rounded-lg p-6 shadow-sm`}
              >
                <h4
                  className={`text-lg font-semibold ${
                    isDark ? "text-purple-400" : "text-purple-800"
                  } mb-3`}
                >
                  �� {t("optimalLevelFound")}
                </h4>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <p>
                      <strong>{t("recommendedLevel")}:</strong>{" "}
                      {analysis.optimalAnalysis.optimalLevel}
                    </p>
                    <p>
                      <strong>{t("totalCost")}:</strong>{" "}
                      {
                        analysis.optimalAnalysis.recommendation
                          ?.totalCostFormatted
                      }
                    </p>
                  </div>
                  <div>
                    <p>
                      <strong>{t("duration")}:</strong>{" "}
                      {
                        analysis.optimalAnalysis.recommendation
                          ?.upgradeDurationFormatted
                      }
                    </p>
                    <p>
                      <strong>{t("timeRatio")}:</strong>{" "}
                      {analysis.optimalAnalysis.recommendation?.ratio}
                    </p>
                  </div>
                </div>
              </div>
            )}

            {/* 🆕 Análise Detalhada EXPANDIDA PARA 1000 LEVELS */}
            <div>
              <button
                onClick={() => setShowDetailedAnalysis(!showDetailedAnalysis)}
                className={`flex items-center gap-2 px-4 py-2 ${
                  isDark
                    ? "bg-gray-700 text-gray-300 hover:bg-gray-600"
                    : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                } rounded-lg transition-colors`}
              >
                <TrendingUp className="w-4 h-4" />
                {showDetailedAnalysis
                  ? t("hideDetailedAnalysis")
                  : t("showDetailedAnalysis")}
              </button>

              {showDetailedAnalysis &&
                analysis.optimalAnalysis.analysis.length > 0 && (
                  <div className="mt-4 space-y-4">
                    {/* 🎯 Resumo de Marcos Importantes */}
                    <div
                      className={`${
                        isDark ? "bg-blue-900/30" : "bg-blue-50"
                      } rounded-lg p-4`}
                    >
                      <h4
                        className={`font-semibold ${
                          isDark ? "text-blue-400" : "text-blue-800"
                        } mb-3`}
                      >
                        🎯 {t("importantMilestones")}
                      </h4>
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-3 text-sm">
                        {analysis.optimalAnalysis.analysis
                          .filter((item) => item.milestone)
                          .slice(0, 8)
                          .map((item, index) => (
                            <div
                              key={index}
                              className={`${
                                isDark
                                  ? "bg-gray-800 border-gray-700"
                                  : "bg-white border"
                              } p-2 rounded`}
                            >
                              <div
                                className={`font-medium ${
                                  isDark ? "text-blue-300" : "text-blue-700"
                                }`}
                              >
                                {item.milestone}
                              </div>
                              <div
                                className={`${
                                  isDark ? "text-gray-400" : "text-gray-600"
                                }`}
                              >
                                {t("level")} {item.targetLevel}
                              </div>
                              <div
                                className={`text-xs ${
                                  isDark ? "text-gray-500" : "text-gray-500"
                                }`}
                              >
                                {item.upgradeDurationFormatted}
                              </div>
                            </div>
                          ))}
                      </div>
                    </div>

                    {/* 🔍 Filtros e Controles */}
                    <div
                      className={`${
                        isDark ? "bg-gray-800" : "bg-gray-50"
                      } rounded-lg p-4`}
                    >
                      <h4 className={`font-semibold ${textClass} mb-3`}>
                        <Filter className="w-4 h-4 inline mr-2" />
                        {t("viewFilters")}
                      </h4>
                      <div className="flex flex-wrap gap-2">
                        <button
                          onClick={() => setLevelFilter("milestones")}
                          className={`px-3 py-1 rounded text-sm transition-colors ${
                            levelFilter === "milestones"
                              ? "bg-blue-500 text-white"
                              : isDark
                              ? "bg-gray-700 text-gray-300 hover:bg-blue-900/50"
                              : "bg-white text-gray-700 hover:bg-blue-100"
                          }`}
                        >
                          🎯 {t("milestone")} (
                          {
                            analysis.optimalAnalysis.analysis.filter(
                              (item) => item.milestone || item.isOptimal
                            ).length
                          }
                          )
                        </button>
                        <button
                          onClick={() => setLevelFilter("first100")}
                          className={`px-3 py-1 rounded text-sm transition-colors ${
                            levelFilter === "first100"
                              ? "bg-blue-500 text-white"
                              : isDark
                              ? "bg-gray-700 text-gray-300 hover:bg-blue-900/50"
                              : "bg-white text-gray-700 hover:bg-blue-100"
                          }`}
                        >
                          🔢 {t("first100")}
                        </button>
                        <button
                          onClick={() => setLevelFilter("every10")}
                          className={`px-3 py-1 rounded text-sm transition-colors ${
                            levelFilter === "every10"
                              ? "bg-blue-500 text-white"
                              : isDark
                              ? "bg-gray-700 text-gray-300 hover:bg-blue-900/50"
                              : "bg-white text-gray-700 hover:bg-blue-100"
                          }`}
                        >
                          📊 {t("every10Levels")}
                        </button>
                        <button
                          onClick={() => setLevelFilter("every50")}
                          className={`px-3 py-1 rounded text-sm transition-colors ${
                            levelFilter === "every50"
                              ? "bg-blue-500 text-white"
                              : isDark
                              ? "bg-gray-700 text-gray-300 hover:bg-blue-900/50"
                              : "bg-white text-gray-700 hover:bg-blue-100"
                          }`}
                        >
                          📈 {t("every50Levels")}
                        </button>
                        <button
                          onClick={() => setLevelFilter("all")}
                          className={`px-3 py-1 rounded text-sm transition-colors ${
                            levelFilter === "all"
                              ? "bg-blue-500 text-white"
                              : isDark
                              ? "bg-gray-700 text-gray-300 hover:bg-blue-900/50"
                              : "bg-white text-gray-700 hover:bg-blue-100"
                          }`}
                        >
                          🚀 {t("all1000")}
                        </button>
                      </div>
                      <p
                        className={`text-xs ${
                          isDark ? "text-gray-400" : "text-gray-500"
                        } mt-2`}
                      >
                        {t("showingLevels")
                          .replace("{0}", getFilteredAnalysis().length)
                          .replace(
                            "{1}",
                            analysis.optimalAnalysis.analysis.length
                          )}
                      </p>
                    </div>

                    {/* 📋 Tabela Expandida */}
                    <div
                      className={`${
                        isDark
                          ? "bg-gray-800 border-gray-700"
                          : "bg-white border"
                      } rounded-lg overflow-hidden shadow-sm`}
                    >
                      <div className="overflow-x-auto max-h-96">
                        <table className="w-full">
                          <thead
                            className={`${
                              isDark ? "bg-gray-700" : "bg-gray-50"
                            } sticky top-0`}
                          >
                            <tr>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("level")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("milestone")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("totalCost")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("totalDuration")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("totalROIPerHour")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("individualCost")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("individualROIPerHour")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("correctionFactor")}
                              </th>
                              <th
                                className={`px-3 py-2 text-left text-xs font-medium ${textClass}`}
                              >
                                {t("status")}
                              </th>
                            </tr>
                          </thead>
                          <tbody
                            className={`${
                              isDark ? "divide-gray-700" : "divide-gray-200"
                            } divide-y`}
                          >
                            {getFilteredAnalysis().map((item, index) => (
                              <tr
                                key={index}
                                className={`
                              ${
                                item.isOptimal
                                  ? isDark
                                    ? "bg-purple-900/20"
                                    : "bg-purple-50"
                                  : ""
                              }
                              ${
                                item.milestone && !item.isOptimal
                                  ? isDark
                                    ? "bg-blue-900/20"
                                    : "bg-blue-50"
                                  : ""
                              }
                              ${
                                isDark
                                  ? "hover:bg-gray-700"
                                  : "hover:bg-gray-50"
                              } transition-colors
                            `}
                              >
                                <td
                                  className={`px-3 py-2 text-xs font-medium ${textClass}`}
                                >
                                  {item.targetLevel}
                                  <span
                                    className={`${
                                      isDark ? "text-gray-500" : "text-gray-400"
                                    } ml-1`}
                                  >
                                    (+{item.levelsAhead})
                                  </span>
                                </td>
                                <td className="px-3 py-2 text-xs">
                                  {item.milestone && (
                                    <span
                                      className={`${
                                        isDark
                                          ? "bg-blue-900/50 text-blue-300"
                                          : "bg-blue-100 text-blue-700"
                                      } px-1 rounded text-xs`}
                                    >
                                      {item.milestone}
                                    </span>
                                  )}
                                </td>
                                <td
                                  className={`px-3 py-2 text-xs font-mono ${textClass}`}
                                >
                                  {item.totalCostFormatted}
                                </td>
                                <td
                                  className={`px-3 py-2 text-xs ${textClass}`}
                                >
                                  {item.upgradeDurationFormatted}
                                </td>
                                <td
                                  className={`px-3 py-2 text-xs font-semibold ${getRoiColor(
                                    item.roiPerHour
                                  )}`}
                                >
                                  {item.roiPerHour}%
                                </td>
                                <td
                                  className={`px-3 py-2 text-xs font-mono ${
                                    isDark ? "text-gray-400" : "text-gray-600"
                                  }`}
                                >
                                  {item.individualLevelCostFormatted}
                                </td>
                                <td
                                  className={`px-3 py-2 text-xs font-semibold ${getRoiColor(
                                    item.individualROI
                                  )}`}
                                >
                                  {item.individualROI}%
                                </td>
                                <td
                                  className={`px-3 py-2 text-xs ${
                                    isDark
                                      ? "text-purple-400"
                                      : "text-purple-600"
                                  }`}
                                >
                                  {item.correctionFactor}
                                </td>
                                <td className="px-3 py-2 text-xs">
                                  {item.isOptimal && (
                                    <span
                                      className={`${
                                        isDark
                                          ? "text-purple-400"
                                          : "text-purple-600"
                                      } font-medium`}
                                    >
                                      ⭐ {t("optimal")}
                                    </span>
                                  )}
                                  {item.milestone && !item.isOptimal && (
                                    <span
                                      className={`${
                                        isDark
                                          ? "text-blue-400"
                                          : "text-blue-600"
                                      } font-medium`}
                                    >
                                      ��
                                    </span>
                                  )}
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>

                      {/* 📊 Estatísticas da Análise */}
                      <div
                        className={`${
                          isDark ? "bg-gray-700" : "bg-gray-50"
                        } p-4 border-t ${
                          isDark ? "border-gray-600" : "border-gray-200"
                        }`}
                      >
                        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                          <div>
                            <span className={`font-medium ${textClass}`}>
                              {t("totalLevels")}
                            </span>
                            <span
                              className={`ml-2 ${
                                isDark ? "text-blue-400" : "text-blue-600"
                              }`}
                            >
                              {analysis.optimalAnalysis.analysis.length}
                            </span>
                          </div>
                          <div>
                            <span className={`font-medium ${textClass}`}>
                              {t("optimalLevel")}
                            </span>
                            <span
                              className={`ml-2 ${
                                isDark ? "text-purple-400" : "text-purple-600"
                              }`}
                            >
                              {analysis.optimalAnalysis.optimalLevel}
                            </span>
                          </div>
                          <div>
                            <span className={`font-medium ${textClass}`}>
                              {t("bestRatio")}
                            </span>
                            <span
                              className={`ml-2 ${
                                isDark ? "text-green-400" : "text-green-600"
                              }`}
                            >
                              {analysis.optimalAnalysis.bestRatio?.toFixed(3)}
                            </span>
                          </div>
                          <div>
                            <span className={`font-medium ${textClass}`}>
                              {t("minerType")}
                            </span>
                            <span
                              className={`ml-2 ${
                                isDark ? "text-orange-400" : "text-orange-600"
                              } capitalize`}
                            >
                              {analysis.minerType}
                            </span>
                          </div>
                        </div>

                        {/* 📈 Estatísticas Adicionais */}
                        <div
                          className={`mt-3 pt-3 border-t ${
                            isDark ? "border-gray-600" : "border-gray-200"
                          }`}
                        >
                          <div className="grid grid-cols-2 md:grid-cols-3 gap-4 text-xs">
                            <div>
                              <span className={`font-medium ${textClass}`}>
                                {t("milestonesIdentified")}
                              </span>
                              <span
                                className={`ml-1 ${
                                  isDark ? "text-gray-400" : "text-gray-600"
                                }`}
                              >
                                {
                                  analysis.optimalAnalysis.analysis.filter(
                                    (item) => item.milestone
                                  ).length
                                }
                              </span>
                            </div>
                            <div>
                              <span className={`font-medium ${textClass}`}>
                                {t("currentFilter")}
                              </span>
                              <span
                                className={`ml-1 ${
                                  isDark ? "text-gray-400" : "text-gray-600"
                                } capitalize`}
                              >
                                {levelFilter}
                              </span>
                            </div>
                            <div>
                              <span className={`font-medium ${textClass}`}>
                                {t("itemsDisplayed")}
                              </span>
                              <span
                                className={`ml-1 ${
                                  isDark ? "text-gray-400" : "text-gray-600"
                                }`}
                              >
                                {getFilteredAnalysis().length}
                              </span>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>

                    {/* 💡 Dicas de Uso */}
                    <div
                      className={`${
                        isDark
                          ? "bg-yellow-900/30 border-yellow-700"
                          : "bg-yellow-50 border-yellow-200"
                      } rounded-lg p-4 border`}
                    >
                      <h5
                        className={`font-semibold ${
                          isDark ? "text-yellow-400" : "text-yellow-800"
                        } mb-2`}
                      >
                        💡 {t("interpretationTips")}
                      </h5>
                      <ul
                        className={`text-sm ${
                          isDark ? "text-yellow-300" : "text-yellow-700"
                        } space-y-1`}
                      >
                        <li>
                          <strong>{t("totalROIPerHour")}:</strong>{" "}
                          {t("totalROIPerHourDescription")}
                        </li>
                        <li>
                          <strong>{t("individualROIPerHour")}:</strong>{" "}
                          {t("individualROIPerHourDescription")}
                        </li>
                        <li>
                          <strong>{t("correctionFactor")}:</strong>{" "}
                          {t("correctionFactorDescription")}
                        </li>
                        <li>
                          <strong>{t("milestone")}:</strong>{" "}
                          {t("milestonesDescription")}
                        </li>
                      </ul>

                      {/* �� SEÇÃO DE AVISOS IMPORTANTES */}
                      <div
                        className={`mt-3 pt-3 border-t ${
                          isDark ? "border-yellow-600" : "border-yellow-300"
                        }`}
                      >
                        <div
                          className={`${
                            isDark
                              ? "bg-red-900/20 border-red-700"
                              : "bg-red-50 border-red-200"
                          } rounded p-3 border space-y-2`}
                        >
                          <p
                            className={`text-sm font-medium ${
                              isDark ? "text-red-400" : "text-red-700"
                            }`}
                          >
                            {t("correctionFactorWarning1")}
                          </p>
                          <p
                            className={`text-sm font-medium ${
                              isDark ? "text-red-400" : "text-red-700"
                            }`}
                          >
                            {t("correctionFactorWarning2")}
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default MinerEfficiencyAnalysis;
